.apps-card {
  background-color: $apps-card___background-color;
  @at-root .card-head {
    display: flex;
    align-items: center;
    padding: 16px 20px;
    color: $card-head___color;
    &--info {
      background-color: $brand-info;
      .fa-exclamation {
        color: $brand-info; } }
    &--success {
      background-color: $brand-success;
      .fa-exclamation {
        color: $brand-success; } }
    &--warning {
      background-color: $brand-warning;
      .fa-exclamation {
        color: $brand-warning; } }
    &--danger {
      background-color: $brand-danger;
      .fa-exclamation {
        color: $brand-danger; } }
    &__title {
      flex-grow: 1;
      margin: 0;
      font-size: 20px;
      text-transform: uppercase;
      font-weight: 400;
      font-kerning: auto; }
    &__apps-count {
      font-size: 18px;
      margin-right: 15px;
      span:first-child {
        white-space: nowrap; }
      span:last-child {
        display: block;
        font-size: 12px;
        text-align: right; } }
    &__icon {
 } }      //
  &__body {
    padding: 0px 20px 25px 20px; }
  &__item {
    margin-top: 15px; } }

@media (max-width: $screen-xs-max) {
    @at-root .card-head {
      &__title {
        font-size: 17px; }
      &__apps-count {
        font-weight: 300;
        font-size: 12px;
        margin-right: 10px;
        span:first-child {
          margin-right: 5px; }
        span:last-child {
          display: inline-block;
          text-transform: uppercase; } } } }
